    line-height: 2.4;
    margin: .25rem;
    padding: 14px 0;
    width: 40px
}

.active .bs-stepper-circle {
    background-color: var(--dashui-primary);
    color: var(--dashui-white)
}

.bs-stepper.vertical {
    display: flex
}

.bs-stepper.vertical .bs-stepper-header {
    align-items: stretch;
    flex-direction: column;
    margin: 0
}

.bs-stepper.vertical .bs-stepper-pane {
    display: block
}

.bs-stepper.vertical .bs-stepper-pane:not(.fade) {
    display: block;
    visibility: hidden
}

.bs-stepper.vertical .content {
    display: block
}

.bs-stepper.vertical .content:not(.fade) {
    display: block;
    visibility: hidden
}

.bs-stepper-pane:not(.fade) {
    display: none
}

.bs-stepper-pane.fade {
    transition-duration: .3s;
    transition-property: opacity;
    visibility: hidden
}

.bs-stepper-pane.fade.active {
    opacity: 1;
    visibility: visible
}

.bs-stepper-pane.active:not(.fade) {
    display: block;
    visibility: visible
}

.bs-stepper-pane.dstepper-block {
    display: block
}

.vertical .bs-stepper-pane.fade.dstepper-none,
.vertical .content.fade.dstepper-none {
    visibility: hidden
}

@media (max-width:520px) {
    .bs-stepper .step-trigger {
        flex-direction: column;
        padding: 10px
    }
    .bs-stepper-header {
        overflow: scroll
    }
    .bs-stepper-content {
        padding: 0
    }
}

@media (max-width:400px) {
    .bs-stepper .line,
    .bs-stepper-line {
        flex-basis: 20px
    }
}

.tagify {
    --tag-bg: var(--dashui-gray-300) !important;
    --tag-hover: var(--dashui-light-primary) !important;
    --tag-invalid-color: var(--dashui-light-danger) !important;
    --tag-text-color: var(--dashui-dark) !important;
    --tags-focus-border-color: var(--dashui-primary) !important;
    --tag-pad: 0.6em 0.5em;
    background-color: var(--dashui-white) !important;
    border-color: var(--dashui-input-border) !important;
    border-radius: .375rem;
    line-height: 1.5
}

.tagify__tag:hover:not([readonly]) div:before {
    bottom: 0;
    left: 0;
    right: 0;
    top: 0
}

.tagify--focus {
    border-color: var(--dashui-primary) !important;
    box-shadow: var(--dashui-input-focus-box-shadow)
}

.tagify__tag {
    background-color: var(--dashui-gray-300);
    border-radius: .375rem;
    color: var(--dashui-white)
}

.tagify__tag__removeBtn:hover {
    background: var(--dashui-light-danger) !important;
    color: var(--dashui-danger) !important
}

